import React from 'react'
import { AppBar, Toolbar, IconButton, Badge, Typography } from "@material-ui/core";
import { ShoppingCart } from "@material-ui/icons";
import { Link, useLocation } from "react-router-dom";

import logo from "../../assets/store.png";
import useStyles from "./styles";

const Navbar = ({totalItems}) => {
    const classes = useStyles();
    const location = useLocation();

    
    
    return (
        <>
            <AppBar position="fixed" className={classes.appBar} color="inherit" />
                <Toolbar variant="regular" position="fixed" className={classes.title} color="inherit">
                    <Typography component={Link} to="/e-commerce">
                        <img src={logo} alt="Mert's Shop" height="25px" className={classes.image} />
                        Mert's Shop
                    </Typography>
                    <div className={classes.grow} />

                    {location.pathname === "/e-commerce" && (
                        <div className={classes.button}>
                            <IconButton component={Link} to="/e-commerce/cart" aria-label="Show cart items" color="inherit">
                                <Badge badgeContent={totalItems} color="secondary">
                                    <ShoppingCart />
                                </Badge>
                            </IconButton>
                        </div>
                    )}
                </Toolbar>
        </>
    )
}



export default Navbar
